.images img {
  cursor: pointer;
}

/* modal */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(27, 27, 27, 0.8);
  display: flex;
  align-items: center;
}
.overlay img {
  display: block;
  max-width: 80%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
}
.overlay > span {
  position: absolute;
  top: 3.5rem;
  right: 2rem;
  font-size: 30px;
  color: #ffffff;
  z-index: 999;
  cursor: pointer;
  opacity: 0.2;
}
.overlay-arrows_left {
  display: flex;
  background-color: transparent;
  justify-content: space-between;
  position: absolute;
  top: 50%;
  width: 50px;
  height: 50px;
  left: 0;
  z-index: 999;
  opacity: 0.2;
  padding-left: 2rem;
}

.overlay-arrows_left svg {
  width: 50px;
  height: 50px;
  cursor: pointer;
  color: #ffffff;
}

.overlay-arrows_right svg {
  width: 50px;
  height: 50px;
  cursor: pointer;
  color: #ffffff;
}

.overlay-arrows_right {
  background-color: transparent;
  justify-content: space-between;
  position: absolute;
  right: 0;
  width: 50px;
  height: 50px;
  top: 50%;
  z-index: 999;
  opacity: 0.2;
}
